interface Item {
  id: string,
  pagename: string,
  URL: string,
  submenu: Array <Object>
}

interface Menu {
  [index: number]: Item
}

export const menu: Menu = [
  { id : "1", pagename : "Home", URL: "#1",submenu: []},
  { id : "2", pagename : "Fresh Food", URL: "#2",submenu: []},
  { id : "3", pagename : "Fresh Vegetables", URL : "#3",
  submenu: [
    { id: "10", pagename: "a", URL: "#10"},
    { id: "11", pagename: "b", URL: "#11"},
    { id: "12", pagename: "c", URL: "#12"},
    { id: 13, pagename: "d", URL: "#13"}
  ]},
  { id : "4", pagename : "Prices drop", URL: "#4",submenu: []},
  { id : "5", pagename : "Contact us",URL: "#5",submenu: []}
];

function ItemMenu ({item}: any) {
  return (
      <a href={item.URL} key={item.id}>{item.pagename}</a>
  )
}
function SubMenu ({sub}: any) {
  return(
      <li>
        <a href={sub.URL} key={sub.id}>{sub.pagename}</a>
      </li>
  )
}
function List({ list }: any) {
  if (!list) {
    return null;
  }
  else if (!list.length) {
    return <p>ko co menu</p>;
  }
  else {
    return (
      <>
        <ul className="nav-menu">
          {
            list.map((item: Item)=> (
              item && item.submenu.length !== 0 ? (
                <li className="sub">
                  <ItemMenu item={item} />
                    {
                      <ul className="submenu"> 
                        {
                          item.submenu.map(sub=>(
                            <SubMenu sub={sub} />
                          ))
                        }
                      </ul>
                    }
                </li>
              ) : (
                <>
                <li>
                  {
                    <ItemMenu item={item} />
                  }
                </li>
              </>
              )
            ))
          }
          </ul>
      </>
    )
  };
}

export default List;