import React from 'react'
import StarRatings from './raiting';
import {trimtext} from './trimwork';

interface Product {
  id: string,
  name: string,
  cover: string,
  stars: number,
  price: number,
  sales: number,
}
  
const Products = ({products}: any) => {
  return (
    <div className="container">
      <div className="products">
        {products.map((product: Product) => (
          <div key={product.id} className="products-card">
            <div className="img-product"><img src={product.cover} alt={product.name} /></div>
            <div className="ctn-card">
              <p className="category">DUMMY Category</p>
              <h5 className="products-title">{trimtext(product.name)}</h5>
                <p className="star">
                  {
                    <StarRatings number={product.stars} />
                  }
                </p>
              <div className="info">
                <p className="old-price">{product.price}$</p>
                <p className="price">{product.price}$</p>
                <p className="sales">{product.sales}</p>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  )
};

export default Products